"use client";

import React, { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import Navbar from "@/components/navbar"; // Import Navbar component

const TasksPage = () => {
  const router = useRouter();
  const [tasks, setTasks] = useState([]);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [status, setStatus] = useState(""); // New status state
  const [error, setError] = useState("");
  const [user, setUser] = useState(null);
  const [editingTask, setEditingTask] = useState(null); // For editing a task
  const [showModal, setShowModal] = useState(false); // To control modal visibility

  useEffect(() => {
    const fetchUserData = async () => {
      const res = await fetch("http://localhost:5001/api/users/check", {
        method: "GET",
        credentials: "include",
      });
      const data = await res.json();
      if (res.ok) {
        setUser(data);
        fetchTasks();
      } else {
        router.push("/login");
      }
    };

    fetchUserData();
  }, [router]);

  const fetchTasks = async () => {
    try {
      const res = await fetch("http://localhost:5001/api/tasks/gettasks", {
        method: "GET",
        credentials: "include",
      });
      const data = await res.json();
      if (res.ok) {
        setTasks(data);
      } else {
        setError(data.message || "Failed to load tasks");
      }
    } catch (err) {
      setError("Something went wrong while fetching tasks");
    }
  };

  const handleCreateTask = async (e) => {
    e.preventDefault();
    try {
      const res = await fetch("http://localhost:5001/api/tasks/CreateTask", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify({ title, description, status }),
      });
      const data = await res.json();
      if (res.ok) {
        setTitle("");
        setDescription("");
        setStatus("");
        fetchTasks();
        setShowModal(false); // Close modal after task creation
      } else {
        setError(data.message || "Failed to create task");
      }
    } catch (err) {
      setError("Something went wrong while creating the task");
    }
  };

  const handleUpdateTask = async () => {
    if (!editingTask) return;
    try {
      const res = await fetch(
        `http://localhost:5001/api/tasks/UpdateTask/${editingTask.id}`,
        {
          method: "PUT",
          headers: { "Content-Type": "application/json" },
          credentials: "include",
          body: JSON.stringify({ title, description, status }),
        }
      );
      const data = await res.json();
      if (res.ok) {
        setEditingTask(null);
        fetchTasks();
        setTitle("");
        setDescription("");
        setStatus("");
        setShowModal(false); // Close modal after updating
      } else {
        setError(data.message || "Failed to update task");
      }
    } catch (err) {
      setError("Something went wrong while updating the task");
    }
  };

  const handleDeleteTask = async (taskId) => {
    try {
      const res = await fetch(
        `http://localhost:5001/api/tasks/DeleteTask/${taskId}`,
        {
          method: "DELETE",
          credentials: "include",
        }
      );
      const data = await res.json();
      if (res.ok) {
        fetchTasks();
      } else {
        setError(data.message || "Failed to delete task");
      }
    } catch (err) {
      setError("Something went wrong while deleting the task");
    }
  };

  const handleEditClick = (task) => {
    setEditingTask(task);
    setTitle(task.title);
    setDescription(task.description);
    setStatus(task.status);
    setShowModal(true); // Show modal for editing
  };

  const closeModal = () => {
    setEditingTask(null);
    setShowModal(false);
    setTitle("");
    setDescription("");
    setStatus("");
  };

  return (
    <div>
      {user && <Navbar user={user} />}
      <div className="max-w-4xl mx-auto p-6">
        <h2 className="text-3xl font-bold mb-6">Your Tasks</h2>

        {/* Buttons to open modal */}
        <div className="mb-4 space-x-4">
          <Button onClick={() => setShowModal(true)} className="cursor-pointer">
            Create Task
          </Button>
        </div>

        {/* Display Tasks */}
        {error && <p className="text-red-600">{error}</p>}
        <div className="space-y-4">
          {tasks.map((task) => (
            <div
              key={task.id}
              className="bg-gray-200 p-4 rounded-md flex justify-between items-center"
            >
              <div>
                <h3 className="font-semibold">{task.title}</h3>
                <p>{task.description}</p>
                <p>Status: {task.status}</p>
              </div>
              <div className="space-x-2">
                <Button
                  onClick={() => handleEditClick(task)}
                  className="bg-yellow-500 text-white hover:bg-yellow-600 cursor-pointer"
                >
                  Edit
                </Button>
                <Button
                  onClick={() => handleDeleteTask(task.id)}
                  className="bg-red-500 text-white hover:bg-red-600 cursor-pointer"
                >
                  Delete
                </Button>
              </div>
            </div>
          ))}
        </div>

        {/* Modal */}
        {showModal && (
          <div className="fixed inset-0 bg-gray-500 bg-opacity-75 flex justify-center items-center">
            <div className="bg-white p-6 rounded-lg w-96">
              <h3 className="text-2xl mb-4">
                {editingTask ? "Edit Task" : "Create Task"}
              </h3>
              <form
                onSubmit={editingTask ? handleUpdateTask : handleCreateTask}
                className="space-y-4"
              >
                <div>
                  <Label htmlFor="title">Title</Label>
                  <Input
                    id="title"
                    type="text"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="description">Description</Label>
                  <Input
                    id="description"
                    type="text"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    required
                  />
                </div>
                <div>
                  <Label htmlFor="status">Status</Label>
                  <Input
                    id="status"
                    type="text"
                    value={status}
                    onChange={(e) => setStatus(e.target.value)}
                    required
                  />
                </div>

                <Button type="submit" className="cursor-pointer">
                  {editingTask ? "Update Task" : "Create Task"}
                </Button>
              </form>
              <Button onClick={closeModal} className="mt-4 w-full cursor-pointer">
                Cancel
              </Button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default TasksPage;
